.35%, #486DEF 74.48%, #7B9AEA 100%);\n$gradient-shadow-primary: drop-shadow(0px 4px 20px rgba(86, 123, 218, 0.5));\n\n/* Events */\n$focus-border: 0px 0px 0px 2px rgba(48, 128, 248, 0.5);\n$focus-inner-border: inset 0px 0px 0px 2px rgba(48, 128, 248, 0.5);\n\n/* Z-index */\n$z-index-max: 99999999999;","@use \"../global/variables.scss\" as *;\n\n.panel {\n  height: 100%;\n  width: 460px;\n  background-color: #fff;\n  border: 1px solid $color-border;\n  box-sizing: border-box;\n  overflow-y: auto;\n  z-index: 9999999;\n}\n// Max width 900px\n@media (max-width: 900px) {\n  .panel {\n    width: 100% !important;\n    position: relative !important;\n    top: 0px;\n    left: 0px;\n    overflow: unset !important;\n  }\n}\n.section {\n  width: 90%;\n  margin: auto;\n  padding-top: 16px;\n  padding-bottom: 16px;\n  border-bottom: 1px solid $color-border;\n  position: relative;\n\n  &:last-child {\n    border-bottom: none;\n  }\n}\n.sectionTitle {\n  margin-top: 8px;\n  margin-bottom: 16px;\n  font-family: $font-bold;\n  color: $color-text-primary;\n}\n.buttonLogout {\n  position: absolute;\n  right: 8px;\n  top: 26px;\n  font-family: $font-medium;\n  color: $color-text-secondary;\n  cursor: pointer;\n}\n.buttonWrap {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n.alert {\n  display: flex;\n  width: 100%;\n  height: 80px;\n  justify-content: space-between;\n  align-items: center;\n  position: relative;\n  overflow: hidden;\n  background-color: rgba(56, 126, 247, 0.1);\n\n  .buttonRight {\n    color: $color-primary;\n    font-family: $font-bold;\n    width: 150px;\n\n    &:hover {\n      cursor: pointer;\n    }\n  }\n}\n.button {\n  display: flex;\n  width: 100%;\n  height: 80px;\n  border-radius: $container-border-radius;\n  border: 1px solid $color-border;\n  justify-content: space-between;\n  align-items: center;\n  position: relative;\n  overflow: hidden;\n\n  &:hover {\n    cursor: pointer;\n    background-color: $color-light-grey;\n  }\n  &:disabled,\n  &[disabled] {\n    background-color: $color-light-grey !important;\n    opacity: 0.5;\n    cursor: not-allowed;\n  }\n  &.progress::after {\n    content: \"\";\n    display: block;\n    position: absolute;\n    background-color: $color-primary;\n    opacity: 0.1;\n    height: 100%;\n    width: 80px;\n    left: 0px;\n    top: 0px;\n  }\n}\n.buttonLeft,\n.buttonRight {\n  width: 60px;\n  display: flex;\n  align-items: center;\n  text-align: center;\n  height: 100%;\n  justify-content: center;\n\n  svg {\n    color: $color-icon;\n  }\n}\n.buttonMiddle {\n  flex: 1;\n\n  .buttonTitle {\n    font-family: $font-bold;\n    color: $color-text-primary;\n  }\n  .buttonDescription {\n    font-family: $font-medium;\n    color: $color-text-secondary;\n    margin-top: 4px;\n  }\n}\n.inputSection {\n  padding-top: 4px;\n  padding-bottom: 4px;\n}\n.inputSectionTitle {\n  font-family: $font-medium;\n  color: $color-text-secondary;\n  margin-bottom: 4px;\n}\n.inputs {\n  display: flex;\n  gap: 18px;\n  margin-bottom: 16px;\n}\n.inputVolume {\n  flex: none !important;\n  width: 100px;\n}\n.input,\n.inputVolume {\n  flex: 1;\n  position: relative;\n\n  input {\n    border-radius: $container-border-radius;\n    border: 1px solid $color-border;\n    height: 40px;\n    box-sizing: border-box;\n    position: relative;\n    width: 100%;\n    padding-left: 18px;\n    padding-right: 40px;\n    font-family: $font-medium;\n  }\n  span {\n    color: $color-text-secondary;\n    font-family: $font-medium;\n    position: absolute;\n    right: 18px;\n    bottom: 12px;\n    user-select: none;\n  }\n}\n.inputTitle {\n  font-family: $font-medium;\n  color: $color-text-secondary;\n  margin-bottom: 8px;\n}\n.uploadArea {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n  margin-bottom: 16px;\n  justify-content: center;\n  align-items: center;\n  width: 100%;\n  border: 1px solid $color-border;\n  border-radius: $container-border-radius;\n  text-align: center;\n  padding-top: 20px;\n  padding-bottom: 20px;\n\n  svg {\n    color: #6e7684;\n  }\n\n  .uploadTitle {\n    font-family: $font-bold;\n    color: $color-text-primary;\n  }\n  .uploadDescription {\n    margin-top: 10px;\n    font-family: $font-medium;\n    color: $color-text-secondary;\n  }\n\n  &:hover {\n    cursor: pointer;\n    background-color: $color-light-grey;\n  }\n}\n.audioDetails {\n  display: flex;\n  border-radius: $container-border-radius;\n  border: 1px solid $color-border;\n  height: 43px;\n  align-items: center;\n  position: relative;\n\n  .audioDetailsLeft {\n    display: flex;\n    padding-left: 14px;\n    padding-right: 8px;\n    svg {\n      color: #6e7684;\n    }\n  }\n  .audioDetailsMiddle {\n    flex: 1;\n    display: flex;\n    flex-direction: column;\n    gap: 4px;\n    overflow: hidden;\n\n    span {\n      text-overflow: ellipsis;\n      overflow: hidden;\n      white-space: nowrap;\n      width: 90%;\n    }\n  }\n  .audioDetailsRight {\n    right: 0px;\n    top: 0px;\n    bottom: 0px;\n    display: flex;\n    align-items: center;\n    padding-right: 14px;\n    position: absolute;\n    height: 100%;\n    svg {\n      color: #6e7684;\n    }\n\n    &:hover {\n      cursor: pointer;\n    }\n  }\n}\n\n.SliderRoot {\n  position: relative;\n  display: flex;\n  align-items: center;\n  user-select: none;\n  touch-action: none;\n  width: 200px;\n  height: 20px;\n  margin-top: 36px;\n  flex: 1;\n}\n\n.SliderTrack {\n  background-color: $color-border;\n  position: relative;\n  flex-grow: 1;\n  border-radius: 9999px;\n  height: 6px;\n}\n\n.SliderRange {\n  position: absolute;\n  background-color: $color-primary;\n  border-radius: 9999px;\n  height: 100%;\n}\n\n.SliderThumb {\n  display: block;\n  width: 16px;\n  height: 16px;\n  background-color: #fff;\n  box-sizing: border-box;\n  border: 2px solid $color-primary;\n  border-radius: 10px;\n}\n.SliderThumb:focus {\n  outline: none;\n  box-shadow: $focus-border;\n}\n\n.updateButton {\n  margin-top: 20px;\n  float: right;\n}\n"],"sourceRo