);\n$container-shadow-focus: 2px 2px 10px rgba(0, 0, 0, 0.1);\n\n/* Gradients */\n$gradient-primary: radial-gradient(117.41% 117.78% at 35.44% 0%, #2BAEF8 23.13%, #3582F6 46.35%, #486DEF 74.48%, #7B9AEA 100%);\n$gradient-shadow-primary: drop-shadow(0px 4px 20px rgba(86, 123, 218, 0.5));\n\n/* Events */\n$focus-border: 0px 0px 0px 2px rgba(48, 128, 248, 0.5);\n$focus-inner-border: inset 0px 0px 0px 2px rgba(48, 128, 248, 0.5);\n\n/* Z-index */\n$z-index-max: 99999999999;","@use \"./variables.scss\" as *;\n\nbody {\n  background: $color-light-grey;\n  font-family: $font-medium;\n  font-size: $font-size-normal;\n  height: 100vh;\n  overflow-y: hidden;\n  margin: 0px;\n}\n\n// max width 900px\n@media (max-width: 900px) {\n  body {\n    overflow-y: unset !important;\n  }\n}\n\n/* Button component */\n.button {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  border-radius: 4px;\n  border: none;\n  padding: 8px 16px;\n  gap: 6px;\n  text-align: center;\n  box-sizing: border-box;\n  border-radius: $container-border-radius;\n  font-family: $font-bold;\n  font-weight: 500;\n\n  &.primaryButton:disabled,\n  &.secondaryButton:disabled {\n    background-color: $color-light-grey !important;\n    color: $color-icon !important;\n    cursor: not-allowed !important;\n    border: 1px solid transparent !important;\n\n    svg {\n      color: $color-icon !important;\n      margin-top: 5px;\n    }\n  }\n\n  &.simpleButton:disabled {\n    opacity: 0.5;\n    cursor: not-allowed !important;\n  }\n\n  &.primaryButton {\n    color: $color-text-contrast;\n    background-color: $color-primary;\n    height: 40px;\n\n    svg {\n      margin-top: 5px;\n      color: $color-text-contrast;\n    }\n    &:hover {\n      background-color: darken($color-primary, 10%);\n      cursor: pointer;\n    }\n  }\n\n  &.secondaryButton {\n    color: $color-text-primary;\n    background-color: #fff;\n    border: 1px solid $color-border;\n    height: 40px;\n    svg {\n      margin-top: 5px;\n      color: $color-icon;\n    }\n    &:hover {\n      background-color: $color-light-grey;\n      cursor: pointer;\n    }\n  }\n\n  &.simpleButton {\n    color: $color-text-secondary;\n    background: none;\n\n    svg {\n      margin-top: 5px;\n    }\n\n    &:hover {\n      background-color: $color-light-grey;\n      cursor: pointer;\n    }\n  }\n\n  &.blueButton {\n    color: $color-primary;\n  }\n\n  &.blackButton {\n    color: $color-text-primary !important;\n  }\n}\n\n/* Define variables for handle size, border width, and margin */\n$handle-size: 15px;\n$border-width: 4px;\n$margin-offset: 8px;\n\n/* Disable transitions for Cropper */\n.cropper,\n.advanced-cropper-artificial-transition,\n.advanced-cropper-background-image,\n.advanced-cropper-wrapper,\n.advanced-cropper,\n.CropperBackgroundWrapper,\n.cropper * {\n  transition: none !important;\n}\n\n.cropper {\n  max-width: 80%;\n  height: 80%;\n  border-radius: 15px;\n  margin: auto !important;\n  position: absolute !important;\n  top: 0px;\n  left: 0px;\n  right: 0px;\n  bottom: 0px !important;\n  display: block;\n  transition: none !important;\n  padding-left: 40px;\n  padding-right: 40px;\n}\n.advanced-cropper-artificial-transition {\n  transition: none !important;\n}\n\n.advanced-cropper-background-image {\n  transition: none !important;\n}\n\n.advanced-cropper-wrapper,\n.advanced-cropper,\n.CropperBackgroundWrapper {\n  /* tile background image svg */\n  background-image: url(\"chrome-extension://__MSG_@@extension_id__/assets/editor/transparenttile.png\") !important;\n  background-repeat: repeat !important;\n  background-size: 20px !important;\n  transition: none !important;\n}\n/* Custom styles for the handles */\n.advanced-cropper-simple-handler {\n  width: $handle-size !important; /* Adjust the handle width */\n  height: $handle-size !important; /* Adjust the handle height */\n  background-color: transparent !important;\n  position: absolute;\n  border-style: solid; /* Specify the border style as solid */\n  border-width: 0 !important; /* Initialize border width to 0 */\n  border-radius: 2px;\n\n  &--west-north,\n  &--west-south {\n    margin-left: $margin-offset !important; /* Offset from the left */\n  }\n\n  &--west-north,\n  &--east-north {\n    margin-top: $margin-offset !important; /* Offset from the top */\n  }\n\n  &--west-south,\n  &--east-south {\n    margin-bottom: $margin-offset !important; /* Offset from the bottom */\n  }\n\n  &--east {\n    margin-right: $margin-offset !important;\n    border-right-width: $border-width !important; /* Border on the right */\n  }\n\n  &--west {\n    margin-left: $margin-offset;\n    border-left-width: $border-width !important; /* Border on the left */\n  }\n\n  &--north {\n    margin-top: $margin-offset;\n    border-top-width: $border-width !important; /* Border on the top */\n  }\n\n  &--south {\n    margin-bottom: $margin-offset;\n    border-bottom-width: $border-width !important; /* Border on the bottom */\n  }\n}\n\n/* reset */\nbutton {\n  all: unset;\n}\n\n.AlertDialogOverlay {\n  background-color: rgba(0, 0, 0, 0.5);\n  position: fixed;\n  inset: 0;\n  animation: overlayShow 150ms cubic-bezier(0.16, 1, 0.3, 1);\n  z-index: 99999999999;\n}\n\n.AlertDialogContent {\n  background-color: white;\n  border-radius: 30px;\n  box-shadow: hsl(206 22% 7% / 35%) 0px 10px 38px -10px,\n    hsl(206 22% 7% / 20%) 0px 10px 20px -15px;\n  position: fixed;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  width: 90vw;\n  max-width: 500px;\n  max-height: 85vh;\n  padding: 35px 25px;\n  animation: contentShow 150ms cubic-bezier(0.16, 1, 0.3, 1);\n  z-index: $z-index-max;\n}\n.AlertDialogContent:focus {\n  outline: none;\n}\n\n.AlertDialogTitle {\n  margin: 0;\n  color: $color-text-primary;\n  font-size: $font-size-normal;\n  font-family: $font-bold;\n  font-weight: 700;\n}\n\n.AlertDialogDescription {\n  margin-bottom: 20px;\n  color: $color-text-secondary;\n  font-size: $font-size-normal;\n  line-height: 1.5;\n\n  a {\n    color: $color-primary !important;\n    font-weight: 600 !important;\n    text-decoration: none !important;\n    display: inline-block;\n    cursor: pointer;\n  }\n}\n\n.Button {\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  border-radius: 30px;\n  padding: 0 15px;\n  font-size: 14px;\n  line-height: 1;\n  font-weight: 500;\n  height: 35px;\n}\n.Button.blue {\n  background-color: rgba(48, 128, 248, 0.1);\n  color: $color-primary;\n\n  &:hover {\n    background-color: rgba(48, 128, 248, 0.15);\n    cursor: pointer;\n  }\n  &:focus {\n    box-shadow: $focus-border;\n  }\n}\n.Button.red {\n  background-color: rgba(247, 56, 90, 0.1);\n  color: rgba(247, 56, 90, 1);\n}\n.Button.red:hover {\n  background-color: rgba(247, 56, 90, 0.15);\n  cursor: pointer;\n}\n.Button.red:focus {\n  box-shadow: $focus-border;\n}\n.Button.grey {\n  background: rgba(110, 118, 132, 0.1);\n  color: $color-text-secondary;\n}\n.Button.grey:hover {\n  background: rgba(110, 118, 132, 0.15);\n  cursor: pointer;\n}\n.Button.grey:focus {\n  box-shadow: $focus-border;\n}\n\n@keyframes overlayShow {\n  from {\n    opacity: 0;\n  }\n  to {\n    opacity: 1;\n  }\n}\n\n@keyframes contentShow {\n  from {\n    opacity: 0;\n    transform: translate(-50%, -48%) scale(0.96);\n  }\n  to {\n    opacity: 1;\n    transform: translate(-50%, -50%) scale(1);\n  }\n}\n\n.SideButtonModal {\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  border-radius: 30px;\n  padding: 0 15px;\n  font-size: 14px;\n  line-height: 1;\n  font-weight: 500;\n  height: 35px;\n  color: $color-text-secondary;\n  font-family: $font-medium;\n\n  &:hover {\n    cursor: pointer;\n    background: rgba(110, 118, 132, 0.05);\n  }\n}\n"],"sourceRoot":""}]);
